import React , { useEffect }from 'react';
import { BrowserRouter, Routes, Route , useLocation} from 'react-router-dom'
import MainPage from './pages/Main.jsx';
import NewsEvents from './pages/NewsEvents.jsx';
import NewsDetails from './components/NewsDetails.jsx';
import './App.css';
import CaseStudies from './pages/CaseStudies.jsx';
import CaseDetails from './components/CaseDetails.jsx';
import TechInsight from './pages/TechInsight.jsx';
import TechInsightDetails from './components/TechInsightDetails.jsx';
import EcosystemsAlliances from './pages/Ecosystems.jsx';
import ContactPage from './pages/Contact.jsx';
import AboutPage from './pages/About.jsx';

const ScrollToTop = () => {
  const { pathname } = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [pathname]);

  return null;
};

const App = () => {
  return (
   <BrowserRouter basename={process.env.PUBLIC_URL}>
     <ScrollToTop />
     <Routes>
        <Route path='/' element={<MainPage/>}></Route>
        <Route path='/about' element={<AboutPage />}></Route>
        <Route path='/newsEvent' element={<NewsEvents/>}></Route>
        <Route path='/newsEvent/:id' element={<NewsDetails/>}></Route>
        <Route path='/caseStudies' element={<CaseStudies />}></Route>
        <Route path='/caseStudies/:id' element={<CaseDetails/>}></Route>
        <Route path='/techInsight' element={<TechInsight/>}></Route>
        <Route path='/techInsight/:id' element={<TechInsightDetails/>}></Route>
        <Route path='/ecosystemsAlliances' element={<EcosystemsAlliances/>}></Route>
        <Route path="/contact" element={<ContactPage />}></Route>
     </Routes>
  </BrowserRouter>
  );
}

export default App;
