import React, { useEffect, useRef, useState } from "react";
import { useLocation } from "react-router-dom";
import Page from "../components/Page.jsx";
import { gsap } from "gsap";
import NavBar from "../components/Nav.jsx";
import AsideNav from "../components/Aside.jsx";
import Footer from "../components/Footer.jsx";
import PartnersShipData from '../json/pages/About.json';

import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination } from "swiper";
import "swiper/css";
import "swiper/css/pagination";

import aboutBackImg from '../assets/imgs/about/about_visual.png';
import aboutScaleImg from '../assets/imgs/about/about_scale.png';
import awardSvg from '../assets/imgs/about/award.svg';
import aboutVideo from '../assets/video/lgcns_service_offering.mp4';



const AboutPage = () => {
  const circleRef = useRef(null);
  const { pathname } = useLocation() 

  const handleSlideChange = () => {
    if (circleRef.current) {
      gsap.to(circleRef.current, {
        rotation: "+=360",
        duration: 1.5,
        ease: "power2.inOut",
      });
    }
  };

  useEffect(() => {
    gsap.utils.toArray("section").forEach((section) => {
      gsap.fromTo(
        section,
        { opacity: 0, y: 100 },
        {
          opacity: 1,
          y: 0,
          scrollTrigger: {
            trigger: section,
            start: "top 30%",
            end: "bottom top",
            onEnter: () => section.classList.add("is_current"),
            onLeave: () => section.classList.remove("is_current"),
            onEnterBack: () => section.classList.add("is_current"),
            onLeaveBack: () => section.classList.remove("is_current"),
            toggleActions: "play none none reverse",
          },
        }
      );
    });
  }, []);

  const [activeIndex, setActiveIndex] = useState(0);

  const keywordSlideChange = (swiper) => {
    setActiveIndex(swiper.realIndex);
  };

  const [aboutMediaLoaded, setAboutMediaLoaded] = useState(false);
  useEffect(() => {
    if (aboutMediaLoaded) {
      window.scrollTo(0, 0);
    }
  }, [aboutMediaLoaded]);

  useEffect(() => {
    const aboutHandleLoad = () => setAboutMediaLoaded(true);

    window.addEventListener("load", aboutHandleLoad);

    return () => window.removeEventListener("load", aboutHandleLoad);
  }, []);

  return (
    <div className="aboutPage">
      <Page header={<NavBar paths={pathname}/>} aside={<AsideNav pathName={pathname}/>} footer={<Footer />}>
        <section
          id="aboutVisualWrap"
          style={{
            backgroundImage: `url(${aboutBackImg})`,
          }}
        >
          <strong>
            About <br />
            LG CNS CLOUD
          </strong>
          <p>
            LG CNS 
            <sub>
                   <br className="br_600" />      .
            </sub>
          </p>
        </section>

        <section
          id="aboutContent1"
          style={{
            backgroundImage: `url(${aboutScaleImg})`,
          }}
        >
          <div className="keywordWrap">
            <p className="left">
              <i className={activeIndex === 0 ? "is_active" : ""}></i>
              <i className={activeIndex === 1 ? "is_active" : ""}></i>
              <i className={activeIndex === 2 ? "is_active" : ""}></i>
            </p>
            <Swiper
              spaceBetween={20}
              slidesPerView={1}
              centeredSlides={true}
              direction={"vertical"}
              loop={true}
              speed={800}
              autoplay={{
                delay: 2000,
                disableOnInteraction: false,
              }}
              modules={[Autoplay, Pagination]}
              onSlideChange={keywordSlideChange}
            >
              <SwiperSlide>
                <strong>Future Innovator</strong>
                <sub>  </sub>
              </SwiperSlide>
              <SwiperSlide>
                <strong>Global Tospan Tier</strong>
                <sub>  </sub>
              </SwiperSlide>
              <SwiperSlide>
                <strong>Trusted spanartner</strong>
                <sub> </sub>
              </SwiperSlide>
            </Swiper>
            <p className="right">
              <i className={activeIndex === 0 ? "is_active" : ""}></i>
              <i className={activeIndex === 1 ? "is_active" : ""}></i>
              <i className={activeIndex === 2 ? "is_active" : ""}></i>
            </p>
          </div>
        </section>
        <section id="aboutContent2">
          <div className="leftWrap">
            <div className="circle" ref={circleRef}></div>
            <span>
              Our <mark>Value</mark>
            </span>
            <p>
              30   IT     <br />
                         .
            </p>
          </div>

          <div className="swiperWrap">
            <Swiper
              spaceBetween={20}
              slidesPerView={3}
              centeredSlides={true}
              direction={"vertical"}
              loop={true}
              speed={800}
              autoplay={{
                delay: 2000,
                disableOnInteraction: false,
              }}
              modules={[Autoplay]}
              onSlideChange={handleSlideChange}
              breakpoints={{
                1300: {
                  slidesPerView: 2.5,
                  spaceBetween: 20,
                },
              }}
            >
              <SwiperSlide>
                <strong>Technology</strong>
                <span> </span>
                <p>
                   CSP 3     <br />
                   .
                </p>
              </SwiperSlide>
              <SwiperSlide>
                <strong>Networking</strong>
                <span> </span>
                <p>
                       <br />
                    .
                </p>
              </SwiperSlide>
              <SwiperSlide>
                <strong>Knowhow</strong>
                <span> </span>
                <p>
                  ,,,,  <br />
                     .
                </p>
              </SwiperSlide>
            </Swiper>
          </div>
        </section>

        <section id="aboutContent3">
          <hgroup>
            <h2>
              <img src={awardSvg} alt="" />
               MSP   <br />
               CSP 3 <br />
                
            </h2>
            <p>
               MSP     CSP 3     <br />
                ,  GenAI, Migraion, Machine Learning    <br />
                   .
            </p>
          </hgroup>
          <div className="swiperWrap">
            <Swiper
              spaceBetween={10}
              slidesPerView={1.5}
              speed={800}
              breakpoints={{
                550: {
                  slidesPerView: 2.8,
                },
                768: {
                  slidesPerView: 3.8,
                },
                1024: {
                  slidesPerView: 4.3,
                },
                1300: {
                  slidesPerView: 5,
                },
                1600: {
                  slidesPerView: 5.3,
                },
                1800: {
                  slidesPerView: 6.2,
                },
              }}
            >
              {PartnersShipData.group1.map((item, index) => (
                <SwiperSlide key={item + index}>
                  <div>
                    <img src={`./assets/imgs/about/partnership/group1/`+item.image} alt={item.title} />
                  </div>
                  <span className="title">{item.title}</span>
                </SwiperSlide>
              ))}
            </Swiper>
            <div className="info">
              <span>
                AWS
                <mark> {PartnersShipData.group1.length}</mark>
              </span>
              <ul>
                <li>4 AWS Competencies</li>
                <li>7 Partner Programs</li>
                <li>8 AWS Service Validations</li>
                <li>700+ AWS Certifications</li>
                <li>100+ AWS Customer Lanches</li>
              </ul>
            </div>
          </div>
          <div className="swiperWrap">
            <Swiper
              spaceBetween={10}
              slidesPerView={1.5}
              speed={800}
              breakpoints={{
                550: {
                  slidesPerView: 2.8,
                },
                768: {
                  slidesPerView: 3.8,
                },
                1024: {
                  slidesPerView: 4.3,
                },
                1600: {
                  slidesPerView: 5.3,
                },
                1800: {
                  slidesPerView: 6.2,
                },
              }}
            >
              {PartnersShipData.group2.map((item , idx) => (
                <SwiperSlide key={item + idx}>
                  <div>
                    <img src={`./assets/imgs/about/partnership/group2/`+item.image} alt={item.title} />
                  </div>
                  <span className="title">{item.title}</span>
                </SwiperSlide>
              ))}
            </Swiper>
            <div className="info">
              <span>
                GCP
                <mark> {PartnersShipData.group2.length}</mark>
              </span>
            </div>
          </div>
          <div className="swiperWrap">
            <Swiper
              spaceBetween={10}
              slidesPerView={1.5}
              speed={800}
              breakpoints={{
                550: {
                  slidesPerView: 2.8,
                },
                768: {
                  slidesPerView: 3.8,
                },
                1024: {
                  slidesPerView: 4.3,
                },
                1600: {
                  slidesPerView: 5.3,
                },
                1800: {
                  slidesPerView: 6.2,
                },
              }}
            >
              {PartnersShipData.group3.map((item , index) => (
                <SwiperSlide key={item + index}>
                  <div>
                    <img src={`./assets/imgs/about/partnership/group3/`+item.image} alt={item.title} />
                  </div>
                  <span className="title">{item.title}</span>
                </SwiperSlide>
              ))}
            </Swiper>
            <div className="info">
              <span>
                MS
                <mark> {PartnersShipData.group3.length}</mark>
              </span>
            </div>
          </div>
        </section>

        <section id="aboutContent4">
          <video src={aboutVideo} autoPlay muted loop playsInline></video>
        </section>
      </Page>
    </div>
  );
};

export default AboutPage;
