import React from "react";
import CheckboxContext from "./CheckboxContext.jsx";

const CheckboxGroup = ({children, values, onChange}) => {

    const isChecked = (value) => values.includes(value);
    const toggleValue = ({ checked, value }) => {
        if (checked) {
            onChange(values.concat(value));
            console.log(value);
            console.log(values.concat(value));
        } else {
            onChange(values.filter((v) => v !== value));
            console.log(value);
            console.log(values.filter((v) => v !== value));
        }
      };

    return (
          <CheckboxContext.Provider value={{ isChecked, toggleValue }}>
            {children}
          </CheckboxContext.Provider>
      );
}

export default CheckboxGroup;